.digital-kit {
  .swiper-container {
    padding: 40px 36px; }
  .swiper-slide {
    background-color: #eeeeee;
    @include tr;
    transform: scale(0.97);
    border-radius: 10px;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
    overflow: hidden;
    &-active {
      background-color: $white;
      transform: scale(1); } }
  .swiper-pagination-fraction {
    color: transparent;
    top: 13px;
    span {
      font-size: 18px;
      line-height: 1;
      color: #9e9e9e;
      padding: 0 10px;
      &.text {
        position: absolute;
        left: 50%;
        transform: translate(-50%); } } }
  .slide__product {
    &__logo {
      @include center;
      padding: 28px 0 40px; }
    &__img {
      @include center;
      padding-bottom: 65px; }
    &__title {
      position: relative;
      &:before {
        content: '';
        width: 60px;
        height: 5px;
        border-radius: 2.5px;
        background-color: #ec135c;
        position: absolute;
        top: -30px;
        left: 50%;
        transform: translate(-50%); }
      h2 {
        font-size: 24px;
        font-weight: 600;
        text-align: center;
        color: #131c2e; }
      p {
        font-size: 18px;
        text-align: center;
        color: #9e9e9e;
        margin-top: 10px;
        margin-bottom: 30px;
        b {
          color: #31cd64;
          font-weight: normal; } } }
    &__function {
      width: 100%;
      overflow-x: scroll;
      &__content {
        display: flex;
        justify-content: space-between;
        padding: 30px;
        height: 125px;
        box-sizing: border-box;
        width: 520px;
        background-color: #f5f5f5; }
      &__img {
        height: 33px;
        @include center; }
      a {
        font-size: 18px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        color: #131c2e; } } } }
